import styled from "styled-components";

export const Center = styled.div`
display:flex;
flex-direction:column;
align-items:${props => props.direction === "horizontal" || props.direction === "all" ? 'center' : 'unset'};
justify-content: ${props => props.direction === "vertical" || props.direction === "all" ? 'center' : 'unset'};
height:100vh;
`;