import { colors } from "./Variables";
import styled from 'styled-components'

export const AppContainer = styled.div`
color:${colors.fontColorLight};
min-height:100vh;
background-size:cover;
background-color: #000000;
background-image: linear-gradient(to right, #000000, #434343, #000000);
font-family: 'Raleway', cursive;
`;

export const Container = styled.div`
display: flex;
flex-direction: row;
flex-wrap:wrap;
justify-content:  ${props => props.justifyContent};
align-items:  ${props => props.alignItems};
gap:15px;
background-color: ${props => props.color || "unset"} ;
`;

export const RelativeContainer = styled.div`
position:relative;
`;


const PositionedContainer = styled.div`
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content:start;
    flex-wrap:wrap;
`;

export const BottomLeftBar = styled(PositionedContainer)`
    bottom: 3px;
    left: 0px;
`;

export const BottomRightBar = styled(PositionedContainer)`
    bottom: 3px;
    right: 0px;
`;

export const TopLeftBar = styled(PositionedContainer)`
    top: 1px;
    left: 0px;
`;

export const TopRightBar = styled(PositionedContainer)`
    top: 1px;
    right: 0px;
`;
