import React from 'react'
import { Badge } from '../../../styled-components/Badge'
import { GenreColors } from '../synthetic-enums'

const GenreBadge = ({ name }) => {
    const color = GenreColors[name];
    return (
        <Badge color={color}>
            <span color={color}>
                {name}
            </span>
        </Badge>
    )
}

export default GenreBadge;