import styled from "styled-components";

export const TransparentBox = styled.div`
background: ${props => props.color};
//background: red;
animation: mymove 5s 5s;

border: 1px solid white;
opacity:0.5;
border-radius:5px;


@keyframes mymove {
    from {background-color: red;}
    to {background-color: blue;}
  }
`;