import styled from "styled-components";

export const Table = styled.table`
width: 100%;
border-collapse: collapse; 

background: linear-gradient(to bottom, #45ab77 25%, #502b4d 75%); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
tbody tr{
    border:5px solid black;
    padding: 10px;
    box-shadow: 5px 3px 18px rgb(221 235 147);
}


/** striped styles : */

 tbody tr:nth-child(odd) {
    // background: #bdc3c7;  /* fallback for old browsers */
    // background: -webkit-linear-gradient(to bottom, #2c3e50, #bdc3c7);  /* Chrome 10-25, Safari 5.1-6 */
    // background: linear-gradient(to bottom, #2c3e50, #bdc3c7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

tbody tr:nth-child(even) {
    // background: #bdc3c7;  /* fallback for old browsers */
    // background: -webkit-linear-gradient(to bottom, #2c3e50, #bdc3c7);  /* Chrome 10-25, Safari 5.1-6 */
    // background: linear-gradient(to bottom, #2c3e50 %25, #bdc3c7 %75); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
} 

`