import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Column, Container, Row } from '../../styled-components/Layout';
import { MenuLink } from '../../styled-components/MenuLink';
import { MenuTitle } from '../../styled-components/MenuTitle';
import { TransparentBox } from '../../styled-components/TransparentBox';
import Collapsible from '../shared/collapsible';
import { ListTypes, RouteKeys } from '../shared/synthetic-enums';

const MoviesMenu = () => {
    const [activeItem, setActiveItem] = useState("searchForMovies");
    let navigate = useNavigate();

    const handleItemClick = (e) => {
        setActiveItem(e.target.name);
        navigate(`/${e.target.name}`);
    };

    return (
        <Column vertical width={"100%"}>
            <Row>
                <MenuTitle>
                    <Collapsible initialCollapsed={true}
                        item="Movies"
                        itemContent={
                            <MenuLink
                                name={RouteKeys.searchForMovies}
                                onClick={handleItemClick}
                                active={activeItem === RouteKeys.searchForMovies}> Search for movies
                            </MenuLink>
                        } />
                </MenuTitle>
            </Row>

            <hr />

            <Row>
                <MenuTitle>
                    <Collapsible initialCollapsed={false}
                        item="My lists"
                        itemContent={
                            <>
                                <Row>
                                    <MenuLink
                                        name={RouteKeys.favoriteMovies}
                                        onClick={handleItemClick}
                                        active={activeItem === RouteKeys.favoriteMovies}> Favorite
                                    </MenuLink>
                                </Row>

                                <Row>
                                    <MenuLink
                                        name={RouteKeys.watchLaterMovies}
                                        onClick={handleItemClick}
                                        active={activeItem === RouteKeys.watchLaterMovies}> Watch Later
                                    </MenuLink>
                                </Row>
                            </>
                        } />
                </MenuTitle>
            </Row>







        </Column >
    )
}

export default MoviesMenu;
